* {
  margin: 0;
  padding: 0;
}

.app {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.board {
  width: calc(64px * 8);
  height: calc(64px * 8);
  display: flex;
  flex-wrap: wrap;
}

.cell {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cell img {
  width: 48px;
  height: 48px;
  position: relative;
}

.black {
  background-color: rgb(86, 77, 77);
}

.white {
  background-color: rgb(238, 189, 130);
}
.selected {
  background-color: brown;
}

.available {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: aquamarine;
}

.lost {
  height: calc(50vh - 100px);
  margin-left: 50px;
  padding: 30px;
  background-color: lightgray;
}